import { FormControl, Validators } from '@angular/forms';
import { SearchfieldComponent } from './searchfield.component';
const meta = {
    title: 'MyLib/Searchfield',
    component: SearchfieldComponent,
    tags: ['autodocs'],
    argTypes: {
        valueChange: { action: 'valueChange' },
        iconClick: { action: 'iconClick' },
        size: { control: { type: 'select' }, options: ['sm', 'md', 'lg'] },
        color: { control: { type: 'select' }, options: ['primary', 'secondary', 'success', 'error', 'focus'] },
        iconStart: { control: 'text' },
        iconEnd: { control: 'text' },
    },
};
export default meta;
// ==========================
// Helper สำหรับสร้าง FormControl
const withControl = (value, required = false) => {
    const validators = [Validators.maxLength(10)];
    if (required)
        validators.push(Validators.required);
    return new FormControl(value || '', { nonNullable: true, validators });
};
// ==========================
// Shared render function
const renderTemplate = (args) => {
    const control = withControl(args.label);
    const valueAction = args.valueChange;
    control.valueChanges.subscribe((val) => valueAction?.(val));
    const templateCode = `
<app-searchfield
  [label]="'${args.label}'"
  [placeholder]="'${args.placeholder}'"
  [control]="control"
  [size]="'${args.size}'"
  [color]="'${args.color}'"
  ${args.iconStart ? `[iconStart]="'${args.iconStart}'"` : ''}
  ${args.iconEnd ? `[iconEnd]="'${args.iconEnd}'"` : ''}>
</app-searchfield>
`;
    return {
        props: { ...args, control },
        template: templateCode,
        parameters: {
            docs: {
                source: { code: templateCode },
            },
        },
    };
};
// ========================
// Stories
export const WithIcons = {
    render: renderTemplate,
    args: { label: 'Search', placeholder: 'Search here', size: 'sm', color: 'primary', iconStart: 'search', iconEnd: 'mic' },
};
export const WithoutIcons = {
    render: renderTemplate,
    args: { label: 'Search', placeholder: 'Search here', size: 'sm', color: 'primary' },
};
// ========================
// Size Stories
export const SizeSmall = {
    render: renderTemplate,
    args: { label: 'Small Field', placeholder: 'Search here', size: 'sm', color: 'primary', iconStart: 'search', iconEnd: 'mic' },
};
export const SizeMedium = {
    render: renderTemplate,
    args: { label: 'Medium Field', placeholder: 'Search here', size: 'md', color: 'primary', iconStart: 'search', iconEnd: 'mic' },
};
export const SizeLarge = {
    render: renderTemplate,
    args: { label: 'Large Field', placeholder: 'Search here', size: 'lg', color: 'primary', iconStart: 'search', iconEnd: 'mic' },
};
// ========================
// Color Stories
export const ColorPrimary = {
    render: renderTemplate,
    args: { label: 'Primary', placeholder: 'Search here', size: 'sm', color: 'primary', iconStart: 'search', iconEnd: 'mic' },
};
export const ColorSecondary = {
    render: renderTemplate,
    args: { label: 'Secondary', placeholder: 'Search here', size: 'sm', color: 'secondary', iconStart: 'search', iconEnd: 'mic' },
};
export const ColorSuccess = {
    render: renderTemplate,
    args: { label: 'Success', placeholder: 'Search here', size: 'sm', color: 'success', iconStart: 'check', iconEnd: 'mic' },
};
export const ColorError = {
    render: renderTemplate,
    args: { label: 'Error', placeholder: 'Search here', size: 'sm', color: 'error', iconStart: 'error', iconEnd: 'mic' },
};
// ========================
// State Stories
export const ErrorState = {
    render: renderTemplate,
    args: { label: 'Error State', placeholder: 'Enter at least 5 characters', size: 'sm', color: 'error' },
    play: async ({ canvasElement }) => {
        const input = canvasElement.querySelector('input');
        if (input) {
            input.value = 'Error State';
            input.dispatchEvent(new Event('input'));
            input.dispatchEvent(new Event('blur'));
        }
    },
};
export const FocusState = {
    render: renderTemplate,
    args: { label: 'Focus State', placeholder: 'Typing...', size: 'sm', color: 'focus', iconStart: 'search' },
    play: async ({ canvasElement }) => {
        const input = canvasElement.querySelector('input');
        if (input)
            input.focus();
    },
};
export const DisabledState = {
    render: (args) => {
        const control = new FormControl({ value: 'Disabled', disabled: true });
        const templateCode = `
<app-searchfield
  [label]="'${args.label}'"
  [placeholder]="'${args.placeholder}'"
  [control]="control"
  [size]="'${args.size}'"
  [color]="'${args.color}'"
  ${args.iconStart ? `[iconStart]="'${args.iconStart}'"` : ''}
  ${args.iconEnd ? `[iconEnd]="'${args.iconEnd}'"` : ''}>
</app-searchfield>
`;
        return { props: { ...args, control }, template: templateCode, parameters: { docs: { source: { code: templateCode } } } };
    },
    args: { label: 'Disabled', placeholder: 'Cannot type', size: 'sm', color: 'primary', iconStart: 'lock' },
};
export const FilledState = {
    render: renderTemplate,
    args: { label: 'Filled State', placeholder: 'Placeholder here', size: 'sm', color: 'success', iconStart: 'check' },
    play: async ({ canvasElement }) => {
        const input = canvasElement.querySelector('input');
        if (input) {
            input.value = 'Hello world';
            input.dispatchEvent(new Event('input'));
        }
    },
};
